.preloader {
  display: block;
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background: #1b283a;
  z-index: 999;
  display: flex;
  justify-content: center;
  align-items: center;
  transition-duration: 0.3s;
}

.preloader-inner {
  position: relative;
  transform: translateX(-50%);
  transition-duration: 1s;
}

.preloader-over-logo {
  background: #1b283a;
  position: absolute;
  right: 0;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: 3;
}

.preloader-over-logo::before {
  content: "";
  display: block;
  position: absolute;
  background: #1b283a;
  right: 0;
  top: 0;
  width: 300%;
  height: 100%;
}

.preloader-over-logo::after {
  content: "";
  display: block;
  position: absolute;
  right: 0;
  top: 0;
  width: 1px;
  height: 100%;
  background: #CCCECD;
  will-change: opacity;
  opacity: 0;
  transition-timing-function: ease-in;
  transition: opacity;
  transition-duration: 0.5s;
}

.preloader-logo {
  padding-left: 24px;
  position: relative;
  z-index: 2;
  opacity: 0;
  will-change: opacity;
  transition-duration: 0.5s;
}

.preloader-logo__top-text,
.preloader-logo__bottom-text {
  transition: transform ease-out 0.5s;
  transform: translateX(-20%);
}

.preloader-logo__top-text {
  width: 100%;
}

.preloader-logo__bottom-text {
  padding-left: 32px;
  margin-top: 13px;
  width: 100%;
}

.preloader--shown .preloader-inner {
  transform: translateX(0%);
}

.preloader--shown .preloader-over-logo {
  animation-duration: 1s;
  animation-name: preloaderOverLogo;
  animation-fill-mode: both;
}

.preloader--shown .preloader-over-logo::after {
  opacity: 1;
}

.preloader--shown .preloader-logo {
  opacity: 1;
}

.preloader--shown .preloader-logo__top-text {
  animation-duration: 1s;
  animation-name: preloaderTopText;
  animation-timing-function: ease-out;
  animation-fill-mode: both;
}

.preloader--shown .preloader-logo__bottom-text {
  animation-duration: 0.8s;
  animation-name: preloaderBottomText;
  animation-timing-function: ease-out;
  animation-fill-mode: both;
}

.preloader--hidden-content .preloader-inner {
  transform: translateX(-50%);
}

.preloader--hidden-content .preloader-over-logo {
  animation-duration: 1s;
  animation-name: preloaderOverLogoOut;
  animation-fill-mode: both;
}

.preloader--hidden-content .preloader-over-logo::after {
  transition-duration: 1s;
  opacity: 0;
}

.preloader--hidden-content .preloader-logo {
  transition-duration: 1s;
  opacity: 0;
}

.preloader--hidden-content .preloader-logo__top-text {
