  animation-name: preloaderTopTextOut;
  animation-timing-function: ease-out;
  animation-fill-mode: both;
}

.preloader--hidden-content .preloader-logo__bottom-text {
  animation-duration: 0.8s;
  animation-name: preloaderBottomTextOut;
  animation-timing-function: ease-out;
  animation-fill-mode: both;
}

.preloader--fully-hidden {
  opacity: 0;
  pointer-events: none;
}

@keyframes preloaderOverLogo {
  0% {
    transform: translateX(0%);
  }

  50% {
    transform: translateX(-105%);
  }

  100% {
    transform: translateX(-100%);
  }
}

@keyframes preloaderTopText {
  0% {
    transform: translateX(-20%);
  }

  50% {
    transform: translateX(10%);
  }

  100% {
    transform: translateX(0%);
  }
}

@keyframes preloaderBottomText {
  0% {
    transform: translateX(-20%);
  }

  50% {
    transform: translateX(15%);
  }

  100% {
    transform: translateX(0%);
  }
}

@keyframes preloaderOverLogoOut {
  0% {
    transform: translateX(-100%);
  }

  100% {
    transform: translateX(0%);
  }
}

@keyframes preloaderTopTextOut {
  0% {
    transform: translateX(0%);
  }

  100% {
    transform: translateX(-20%);
  }
}

@keyframes preloaderBottomTextOut {
  0% {
    transform: translateX(0%);
  }

  100% {
    transform: translateX(-20%);
  }
}

